#shutdown-screen {
    position: fixed;
    top:0;
    left: 0;
    width: 99.3vw;
    height: 100vh;
    background-color: var(--transparent-black);
}

#send-mail-title {
    margin: 1vw;
    color: var(--text-blue);
    text-align: left;
}

#send-mail-title-div {
    width: 95%;
    margin-right: 1vw;
    justify-content: space-around;
}

#template-button {
    width: 5vw;
}

#send-mail {
    position: fixed;
    top: 1vw;
    left: 1vw;
    width: 50vw;
    padding-bottom: 1vw;
    border: solid 0.2vw var(--text-black);
    border-radius: 2vw;
    background-color: var(--text-white);
}

#email-area {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: none;
    resize: none;
    border: none;
    height: 12vw !important;
    margin-bottom: 0;
}

#template-holder {
    position: fixed;
    top: 1vw;
    left: 52vw;
    overflow: hidden;
}

#subject-fieldset, #email-area {
    width: 40vw;
}

.list-picker-mail {
    box-shadow: var(--text-grey) 0.3vw 0.3vw !important;
    border: solid 0.15vw var(--text-black) !important;
}

.email-fieldset {
    height: auto !important;
}
.black-background {
    position: fixed;
    width: calc(100vw - 200px);
    height: 100vh;
    background-color: var(--transparent-black);
    z-index: 50;
}

#account-list {
    padding-top: 1vw;
}

#nav-bar {
    position: fixed;
    right: 0;
    width: 200px;
    min-height: 100vh;
    background-color: var(--text-blue);
    border-left: solid 0.2vw var(--text-black);
}

#email-info {
    position: fixed;
    top: 20px;
    left: 20px;
    min-width: 320px;
    padding-bottom: 10px;
    border: solid 2px var(--text-black);
    border-radius: 20px;
    background-color: var(--text-white);
}

#detalhes-email {
    font-size: 20px;
}

#login {
    padding: 1vw;
    border: solid 0.1vw var(--text-blue);
    border-radius: 1vw;
    background: var(--text-white);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 320px;
}

#login-logo {
    min-height: 70px;
    height: 5vw;
    margin: 1vw;
    margin-top: 0;
}

.background-block {
    background: var(--transparent-black);
    width: 100vw;
    height: 100vh;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
}
.red-fieldset {
    border-color: var(--hover-blue);
    border-radius: 10px;
    border-width: 2px;
    border-style: solid;
    margin: 5px;
    width: 100px;
    height: 35px;
    cursor: text;
}

.red-legend {
    color: var(--text-blue);
    font-size: 15px;
    margin-left: 3px;
}

.transparent-input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    background: none;
    outline: none;
    width: 90px;
    height: 20px;
}

.button-fixed {
    width: 80px !important;
    height: 20px !important;
    font-size: 15px !important;
    margin: 5px !important;
    padding: 5px !important;
    border-radius: 10px !important;
}

.big-fieldset {
    margin: 7px !important;
}

.big-input {
    width: 250px !important;
    height: 20px !important;
    font-size: 13px !important;
}

.big-legend {
    font-size: 18px !important;
}

#controlled-form,
#controlled-search {
    background-color: var(--text-white);
    border-radius: 20px;
    margin: 10px;
    height: 235px;
    width: 150px;
    padding: 7px;
    border: solid 2px var(--text-black);
    box-shadow: var(--shadow-blue) 3px 5px;
}

#controlled-search {
    height: 105px !important;
}

.add-button {
    text-align: center;
    font-size: 15px;
    font-family: dark;
    margin: 0.5vw;
    padding: 0.6vw;
    border-radius: 0.8vw;
    color: var(--hover-blue);
    border: solid 2px var(--text-black);
    cursor: pointer;
    box-shadow: var(--text-grey) 3px 4px;
}

.add-button:hover {
    color: var(--text-white);
    background-color: var(--hover-blue);
}

#detalhes-email {
    margin: 0;
    margin-top: 1vw;
}

#list-picker {
    font-family: light, sans-serif !important;
    width: 170px !important;
    height: 40px !important;
    font-size: 15px !important;
    padding: 5px !important;
    box-shadow: var(--shadow-blue) 3px 5px !important;
    outline: none;
}
.td-space-before::before {
    content: "";
    margin-right: 0.5vw;
}

.td-space-after::after {
    content: "";
    margin-left: 0.5vw;
}

/******************************/

.brd-bot {
    border-bottom: solid 0.1vw var(--text-black);
}

.brd-right {
    border-right: solid 0.1vw var(--text-black);
}

.brd-round {
    border: solid 0.1vw var(--text-blue);
    border-radius: 0.5vw;
    padding: 0.3vw;
}

/******************************/

.font-info {
    font-family: sans-serif;
}

/******************************/

#account {
    margin: 1vw;
    margin-bottom: 0.2vw;
    margin-top: 0.2vw;
    cursor: pointer;
}

#account:hover {
    border-color: var(--text-black);
    background-color: var(--hover-blue);
}

#account-list {
    width: calc(98vw - 200px);
    height: 100%;
}

#nav-button, #list-picker {
    font-family: dark, sans-serif;
    width: 160px;
    height: 30px;
    font-size: 15px;
    box-shadow: var(--shadow-blue) 3px 5px !important;
    border-radius: 10px;
    margin: 5px;
    color: var(--hover-blue);
    border: solid 2px black;
    background-color: var(--text-white);
    cursor: pointer;
}

#nav-button:hover {
    color: var(--text-white);
    background-color: var(--hover-blue);
}

:root {
    --text-black: black;
    --text-white: white;
    --text-grey: #888888;
    --text-blue: #0d80c2;

    --shadow-blue: #0e4e74;
    --hover-blue: #5faedb;

    --transparent: rgba(255, 255, 255, 0.9);
    --transparent-black: rgba(0, 0, 0, 0.8);
}

@font-face {
    font-family: light;
    src: url(/static/media/light.042dc5d4.woff);
}

@font-face {
    font-family: dark;
    src: url(/static/media/bold.c35c6c90.woff);
}

body {
    font-family: light, sans-serif;
    background-image: url(https://i.imgur.com/MOusKYj.jpg);
    background-repeat: repeat-y;
    background-size: contain;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-family: light, sans-serif;
    font-size: 13px;
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder {
    font-family: light, sans-serif;
    font-size: 13px;
}

input::placeholder, textarea::placeholder {
    font-family: light, sans-serif;
    font-size: 13px;
}

input, textarea {
    font-family: light, sans-serif;
    font-size: 13px;
    padding: 2px;
    padding-top: 1px;
}

input {
    height: 1.5vw;
    width: 8vw;
}

h1 {
    font-size: 2vw;
}

.dis-flex,
.dis-flex-col,
.dis-flex-align,
.dis-flex-justify {
    display: flex;
}

.dis-flex-col {
    flex-direction: column;
}

.dis-flex-align {
    align-items: center;
}

.dis-flex-justify {
    justify-content: center;
}

/******************************/

.text-xs {
    font-size: 1vw;
}

.text-sm {
    font-size: 1.2vw;
}

.align-right {
    text-align: right;
}

.align-center {
    text-align: center;
}

/******************************/

.he-2 {
    height: 2vw;
}

/******************************/

.td-3 {
    width: 33.33%;
}

/******************************/

#app {
    width: 100%;
    min-height: 100vh;
    background-color: rgba(255, 255, 255, 0.9);
    background-color: var(--transparent);
}

